import ewf from 'ewf';
import ShipmentPrintController from './shipment-print-controller';
import './../../../directives/ewf-pdf/ewf-pdf-directive';
import './../../../directives/ewf-validate/ewf-validate-pattern-directive';
import './../../../directives/ewf-input/ewf-input-directive';
import './../../../directives/ewf-form/ewf-form-directive';

ewf.directive('ewfShipmentPrint', ewfShipmentPrint);

function ewfShipmentPrint() {
    return {
        restrict: 'E',
        controller: ShipmentPrintController,
        controllerAs: 'printCtrl',
        template:'<div class=container ewf-form=shipmentPrint><ul class=steppable-breadcrumbs><li class=\"steppable-breadcrumbs__item is-complete\" nls=shipment.create></li><li class=\"steppable-breadcrumbs__item is-complete\" nls=shipment.pay></li><li class=\"steppable-breadcrumbs__item is-current\" nls=shipment.print></li></ul><section class=area ng-if=printCtrl.error><h2 class=\"area__title h2\" nls={{printCtrl.error}} nls-bind></h2></section><section class=area ng-if=!printCtrl.error><h2 class=\"area__title h2 margin-none\" nls=shipment.print_and_you_are_done></h2><div class=row><div class=col-9><div class=overlay-grey><div class=print-preview><header class=print-preview__header><h2 class=print-preview__title nls=shipment.print_and_preview></h2><p nls=shipment.print_labels_and_customs_paperwork></p></header><div><div class=print-preview__item ng-if=printCtrl.waybillDocument><div ng-click=printCtrl.previewDocument(printCtrl.DOCUMENT_TYPES.WAYBILL)><img class=print-preview__img src=/etc/clientlibs/dhl/global/public/img/waybill.jpg> <i class=\"dhlicon-search print-preview__icon\"></i></div><label class=checkbox><input id=check_waybill class=checkbox__input name=waybill type=checkbox checked disabled data-aqa-id=check_waybill> <span class=label nls=shipment.print_label></span></label><div class=print-preview__copies-number><input type=text class=input size=3 data-min=1 data-input=number ng-model=printCtrl.waybillQuantity ewf-input=shipmentPrint.waybillQuantity ewf-validate-pattern={{printCtrl.PATTERNS.numeric}}> <span nls=shipment.print_copy></span></div></div><div class=print-preview__item ng-if=printCtrl.invoiceDocument><div ng-click=printCtrl.previewDocument(printCtrl.DOCUMENT_TYPES.INVOICE)><img class=print-preview__img src=/etc/clientlibs/dhl/global/public/img/invoice.jpg> <i class=\"dhlicon-search print-preview__icon\"></i></div><label class=checkbox><input id=check_invoice class=checkbox__input name=invoice type=checkbox checked disabled data-aqa-id=check_invoice> <span class=label nls=shipment.print_invoice></span></label><div class=print-preview__copies-number><input type=text class=input size=3 data-min=1 data-input=number ng-model=printCtrl.invoiceQuantity ewf-input=shipmentPrint.invoiceQuantity ewf-validate-pattern={{printCtrl.PATTERNS.numeric}}> <span nls=shipment.print_copy></span></div></div><div class=print-preview__item ng-if=printCtrl.receiptDocument><div ng-click=printCtrl.previewDocument(printCtrl.DOCUMENT_TYPES.RECEIPT)><img class=print-preview__img src=/etc/clientlibs/dhl/global/public/img/receipt.jpg> <i class=\"dhlicon-search print-preview__icon\"></i></div><label class=checkbox><input id=check_receipt class=checkbox__input name=receipt type=checkbox ng-model=printCtrl.printReceiptSelected data-aqa-id=check_receipt> <span class=label nls=shipment.print_receipt></span></label><div class=print-preview__copies-number><input class=input type=text size=3 data-min=1 data-input=number ng-model=printCtrl.receiptQuantity ewf-input=shipmentPrint.receiptQuantity ewf-validate-pattern={{printCtrl.PATTERNS.numeric}}> <span nls=shipment.print_copy></span></div></div></div><button class=\"btn btn_success\" type=button ng-if=printCtrl.documentsAssigned ng-click=printCtrl.print()><i class=dhlicon-print></i><span nls=shipment.print_selected_documents></span></button></div></div></div><div class=col-3><div class=overlay-white><div><h3 class=\"h3 margin-none\" nls=shipment.print_your_tracking_number></h3><h2 class=\"trackingNumber margin-none\">{{printCtrl.trackingNumber.trackingNumber}}</h2><ul class=no-bullets ng-repeat=\"pieceNumber in printCtrl.trackingNumber.pieceNumbers\"><li><span><span nls=shipment.print_piece_number></span>{{$index + 1}}: {{pieceNumber}}</span></li></ul><input name=trackingNumber type=hidden value=6573285945></div><hr><div><h3 class=\"h3 margin-none\" nls=shipment.print_pickup_scheduled_for></h3><p class=margin-bottom-none><b>{{printCtrl.pickUpDate}}</b><br>Between <b>10:30 am</b> and <b>8:00 pm</b><br>Pickup Confirmation Number: {{printCtrl.pickUpNumber}}<br><br><a href=# class=\"block margin-top\" nls=shipment.print_edit></a></p></div></div><ul class=no-bullets><li><a class=\"btn btn_action full-width a-left\" ng-click=printCtrl.saveAsFavorite()><i class=dhlicon-favorite-empty></i> <span nls=shipment.print_save_as_favorite></span></a></li></ul></div></div></section></div>'
    };
}
